import React from "react";

import "./operationsbox.css";

export const OperationsBox = ({ selectedOperation, setSelectedOperation }) => {
	const operationNames = [
		"Agent \u2194 Truth",
		"Other \u2194 Agent",
		"Product \u2194 Other",
		"Truth \u2194 Product",
	];

	const displayOperationList = () => {
		return operationNames.map((item, index) => {
			let operationClass =
				selectedOperation === index
					? "operation-name-selected"
					: "operation-name";
			return (
				<div
					className={operationClass}
					onClick={() => setSelectedOperation(index)}
				>
					{selectedOperation === index ? "☑ " : "☐ "}
					{item}
				</div>
			);
		});
	};
	return (
		<div className="operations-box">
			<h4 className="operations-list-title">&nbsp;&nbsp;Selected Operation:</h4>
			<div className="operations-list">{displayOperationList()}</div>
		</div>
	);
};
