import React from "react";

import { DiscourseSelector } from "../discourseselector";
import { DiscourseDisplay } from "../discoursedisplay";
import { Overview } from "../overview";
import useGrid from "./useGrid";
import { ringNameDisplay } from "../../elements/ringnamedisplay";
import { OperationsBox } from "../../elements/operationsbox";

import { tavarish } from "../../../assets";
import { linarrow } from "../../../assets";
import { cwarrow } from "../../../assets";

import "./discoursegrid.css";
import { GraphSettings } from "../../elements/graphsettings/graphsettings";

export const DiscourseGrid = ({
	setTableOpen,
	namedDiscourses,
	discourseNames,
	setNewDiscourse,
	tableOpen,
	overviewOpen,
	setOverviewOpen,
	graphSettingsOpen,
	setGraphSettingsOpen,
	ringNames,
	ringSequences,
	selectedOperation,
	setSelectedOperation,
	graphData,
	setGraphData,
	graphInfo,
	setGraphInfo,
}) => {
	let {
		selectorProps,
		discourseList,
		quickAdd,
		selectedMathemes,
		setSelectedMathemes,
		rotateDiscourse,
		customOperation,
		handleRotate,
		handleClear,
		handleRenameRing,
		handleCreateGraph,
		reorder,
		handleToggle,
	} = useGrid({
		tableOpen,
		setTableOpen,
		discourseNames,
		namedDiscourses,
		setNewDiscourse,
		overviewOpen,
		setOverviewOpen,
		ringNames,
		ringSequences,
		selectedOperation,
		setSelectedOperation,
		setGraphData,
		graphInfo,
		setGraphInfo,
	});

	const displayRing = (inner = false) => {
		if (inner) {
			return (
				<>
					<DiscourseSelector
						{...selectorProps}
						{...discourseList}
						quickAdd={quickAdd}
					/>
					{[1, 2, 3].map((rotation) => (
						<DiscourseDisplay
							displayValues={rotateDiscourse(
								selectedMathemes,
								rotation
							)}
							{...discourseList}
							setNewDiscourse={setNewDiscourse}
							quickAdd={quickAdd}
						/>
					))}
				</>
			);
		} else
			return [...Array(4).keys()].map((rotation) => (
				<div
					className={`custom-operation${
						rotation === 0 ? "" : rotation + 1
					}`}
				>
					<DiscourseDisplay
						displayValues={rotateDiscourse(
							customOperation(
								selectedMathemes,
								selectedOperation
							),
							rotation
						)}
						{...discourseList}
						quickAdd={quickAdd}
					/>
				</div>
			));
	};

	const displayCwArrows = (arrowClass, order = [1, 2, 3, 4]) =>
		order.map((index) => (
			<div className={`image-container ${arrowClass}-image-container`}>
				<img
					className={`${arrowClass}-${index}`}
					src={cwarrow}
					alt="clockwise arrow"
					draggable="false"
				/>
			</div>
		));

	return (
		<div className="outer-grid">
			<div className="menu">
				<button
					className="nametable-open"
					onClick={() => handleToggle(setTableOpen)}
				>
					Custom Discourses
				</button>
				<button
					className="overview-open"
					onClick={() => handleToggle(setOverviewOpen)}
				>
					Overview
				</button>

				<button
					className="graph-settings-open"
					onClick={() => handleToggle(setGraphSettingsOpen)}
				>
					3D Graph
				</button>


			</div>

			{displayRing()}

			<div className="image-container linarrow-image-container">
				<img
					className="linarrow1"
					src={linarrow}
					alt="arrow"
					draggable="false"
				/>
			</div>
			{displayCwArrows("cwarrowout")}

			<div className="discourse-grid">
				{displayRing(true)}
				<div className="image-container stalin-image-container">
					<img
						className="stalin"
						src={tavarish}
						alt="comrade stalin ripping it"
						title="comrade stalin ripping it"
						draggable="false"
					/>
				</div>

				{displayCwArrows("cwarrow", [1, 2, 4, 3])}
			</div>

			{selectedMathemes.includes(" ") ? null : (
				<React.Fragment>
					<div
						className="outer-ring-title"
						title="Click to rename ring"
						onClick={() =>
							handleRenameRing(
								customOperation(
									selectedMathemes,
									selectedOperation
								)
							)
						}
					>
						Outer ring:
						<h2>
							{ringNameDisplay(
								ringNames,
								ringSequences,
								customOperation(
									selectedMathemes,
									selectedOperation
								),
								reorder
							)}
						</h2>
					</div>
					<div
						className="inner-ring-title"
						title="Click to rename ring"
						onClick={() => handleRenameRing(selectedMathemes)}
					>
						Inner ring:
						<h2>
							{ringNameDisplay(
								ringNames,
								ringSequences,
								selectedMathemes,
								reorder
							)}
						</h2>
					</div>
				</React.Fragment>
			)}

			<div className="controls">
				<button
					className="ccw-button generic-button"
					title="rotate counterclockwise (previous outer ring)"
					onClick={() => handleRotate("ccw")}
				>
					&#10226;
				</button>
				<button
					className="cw-button generic-button"
					title="rotate clockwise (next outer ring)"
					onClick={() => handleRotate("cw")}
				>
					&#10227;
				</button>
				<button
					className="clear-button generic-button"
					onClick={handleClear}
				>
					Clear Input
				</button>
			</div>
			{overviewOpen ? (
				<Overview
					discourseList={discourseList}
					rotateDiscourse={rotateDiscourse}
					setSelectedMathemes={setSelectedMathemes}
					handleCloseOverview={() => handleToggle(setOverviewOpen)}
					ringNames={ringNames}
					ringSequences={ringSequences}
					customOperation={customOperation}
					reorder={reorder}
					handleRenameRing={handleRenameRing}
				/>
			) : null}
			<OperationsBox
				selectedOperation={selectedOperation}
				setSelectedOperation={setSelectedOperation}
			/>
			{graphSettingsOpen ? (
				<GraphSettings
					graphInfo={graphInfo}
					setGraphInfo={setGraphInfo}
				/>
			) : null}
		</div>
	);
};
