import React, { useState, useEffect } from "react";
import Cookies from "universal-cookie";

import { DiscourseGrid } from "../../components/compounds/discoursegrid";
import { NameTable } from "../../components/compounds/nametable";
import {
	namedDiscourses,
	discourseNames,
	ringNames,
	ringSequences,
} from "../../discourselist.json";
import { InfoBox } from "../../components/elements/infobox/infobox";
import "./Home.css";

const Home = ({ graphData, setGraphData, graphInfo, setGraphInfo }) => {
	const cookies = new Cookies();
	const [displayState, setDisplayState] = useState("nodisplay");
	const [tableOpen, setTableOpen] = useState(false);
	const [overviewOpen, setOverviewOpen] = useState(false);
	const [graphSettingsOpen, setGraphSettingsOpen] = useState(false);
	const [infoBoxState, setInfoBoxState] = useState(false);
	const [newDiscourse, setNewDiscourse] = useState([" ", " ", " ", " "]);
	const [selectedOperation, setSelectedOperation] = useState(0);
	

	let namesCookie = cookies.get("discourseNames");
	let discourseCookie = cookies.get("namedDiscourses");
	let ringCookie = cookies.get("ringNames");
	let sequenceCookie = cookies.get("ringSequences");

	useEffect(() => {
		if (!namesCookie)
			cookies.set("discourseNames", JSON.stringify(discourseNames), {
				path: "/",
			});
		if (!discourseCookie)
			cookies.set("namedDiscourses", JSON.stringify(namedDiscourses), {
				path: "/",
			});
		if (!ringCookie)
			cookies.set("ringNames", JSON.stringify(ringNames), {
				path: "/",
			});
		if (!sequenceCookie)
			cookies.set("ringSequences", JSON.stringify(ringSequences), {
				path: "/",
			});
	}, []);

	const isItTime = () => {
		let time = new Date();
		var hours = time.getHours();
		var minutes = time.getMinutes();
		if (hours === 4 && minutes === 20) setDisplayState("display");
		else setDisplayState("nodisplay");
	};

	useEffect(() => {
		let interval = setInterval(() => isItTime(), 1000);
		return () => {
			clearInterval(interval);
		};
	}, []);

	const handleCloseTable = () => {
		setTableOpen(false);
	};

	const handleTableReset = () => {
		if (!window.confirm("Do you wish to reset all data?")) return;
		cookies.set("discourseNames", JSON.stringify(discourseNames), {
			path: "/",
		});

		cookies.set("namedDiscourses", JSON.stringify(namedDiscourses), {
			path: "/",
		});
		cookies.set("ringNames", JSON.stringify(ringNames), {
			path: "/",
		});
		cookies.set("ringSequences", JSON.stringify(ringSequences), {
			path: "/",
		});
		handleCloseTable();
	};

	return (
		<div className="main-container">
			<div className={`blaze-${displayState}`}>
				<p>420</p>
				<p>BLAZE IT!!!</p>
			</div>
			<DiscourseGrid
				setTableOpen={setTableOpen}
				tableOpen={tableOpen}
				namedDiscourses={discourseCookie}
				discourseNames={namesCookie}
				ringNames={ringCookie}
				ringSequences={sequenceCookie}
				setNewDiscourse={setNewDiscourse}
				overviewOpen={overviewOpen}
				setOverviewOpen={setOverviewOpen}
				graphSettingsOpen={graphSettingsOpen}
				setGraphSettingsOpen={setGraphSettingsOpen}
				selectedOperation={selectedOperation}
				setSelectedOperation={setSelectedOperation}
				graphData={graphData}
				setGraphData={setGraphData}
				graphInfo={graphInfo}
				setGraphInfo={setGraphInfo}
			/>

			{tableOpen ? (
				<NameTable
					handleCloseTable={handleCloseTable}
					handleTableReset={handleTableReset}
					setNewDiscourse={setNewDiscourse}
					newDiscourse={newDiscourse}
				/>
			) : null}
			<button
				title="Readme"
				className="info-toggle"
				onClick={() => setInfoBoxState((prevstate) => !prevstate)}
			>
				[v1.0]
			</button>
			{infoBoxState ? (
				<InfoBox
					infoBoxState={infoBoxState}
					setInfoBoxState={setInfoBoxState}
				/>
			) : null}
		</div>
	);
};

export default Home;
