import React, { useState, useEffect } from "react";
import Cookies from "universal-cookie";
import Draggable from "react-draggable";
import { TableRow } from "../../elements/tablerow";
import { TableInput } from "../../compounds/tableinput";

import "./nametable.css";

const NameTableTitle = ({ handleCloseTable }) => {
	return (
		<div className="name-table-title">
			<p>Add Custom Discourses: </p>
			<div className="close-button text-button" onClick={handleCloseTable}>
				[<b>&#x2613;</b>]
			</div>
		</div>
	);
};

const NameTableHeader = () => {
	return (
		<React.Fragment>
			<span>Name</span>
			<span>Agent</span>
			<span>Other</span>
			<span>Product</span>
			<span>Truth</span>
			<span>&nbsp;</span>
		</React.Fragment>
	);
};

const NameTableBody = ({
	namedDiscourses,
	discourseNames,
	removeDiscourse,
}) => {
	return namedDiscourses.map((row, index) => (
		<TableRow
			row={row}
			discourseName={discourseNames[index]}
			removeDiscourse={removeDiscourse}
		/>
	));
};

export const NameTable = ({
	handleCloseTable,
	handleTableReset,
	newDiscourse,
	setNewDiscourse,
}) => {
	const cookies = new Cookies();
	const [namedDiscourses, setNamedDiscourses] = useState(
		cookies.get("namedDiscourses") || []
	);
	const [discourseNames, setDiscourseNames] = useState(
		cookies.get("discourseNames") || []
	);

	const [nameInput, setNameInput] = useState("");

	const [exportData, setExportData] = useState("");
	const [placeholder, setPlaceholder] = useState(
		"Paste data here to import. Or click export to get your data."
	);

	useEffect(() => {
		updateDiscourses({
			nextDiscourseNames: cookies.get("discourseNames"),
			nextNamedDiscourses: cookies.get("namedDiscourses"),
		});
	}, [handleTableReset]);

	const importDiscourses = () => {
		const data = JSON.parse(exportData);
		setExportData(data);
	};

	const exportDiscourses = () => {
		const data = JSON.stringify(cookies.getAll());
		setExportData(data);
	};

	useEffect(() => {
		if (exportData === 420) {
			setExportData("");
			setPlaceholder("Blaze it faggot!");
		}
		// alttaki gibi yazınca neden anında update ediyor?
		// if (exportData === "420") setExportData("Blaze it faggot");
		if (
			!exportData ||
			!exportData.discourseNames ||
			!exportData.namedDiscourses ||
			!exportData.ringNames ||
			!exportData.ringSequences
		)
			return;
		cookies.set(
			"discourseNames",
			JSON.stringify(exportData.discourseNames),
			{
				path: "/",
			}
		);
		cookies.set(
			"namedDiscourses",
			JSON.stringify(exportData.namedDiscourses),
			{
				path: "/",
			}
		);
		cookies.set(
			"ringNames",
			JSON.stringify(exportData.ringNames),
			{
				path: "/",
			}
		);
		cookies.set(
			"ringSequences",
			JSON.stringify(exportData.ringSequences),
			{
				path: "/",
			}
		);
		setExportData("");
		setPlaceholder(
			"Imported successfully. Close and reopen this window to refresh the table."
		);
	}, [importDiscourses]);

	const discourseExists = () => {
		return namedDiscourses.some(
			(discourse) =>
				discourseNames.includes(nameInput) ||
				discourse.every((value, index) => value === newDiscourse[index])
		);
	};

	const updateDiscourses = ({ nextDiscourseNames, nextNamedDiscourses }) => {
		setDiscourseNames(Object.assign([], nextDiscourseNames));
		setNamedDiscourses(Object.assign([], nextNamedDiscourses));

		cookies.set("discourseNames", JSON.stringify(nextDiscourseNames), {
			path: "/",
		});
		cookies.set("namedDiscourses", JSON.stringify(nextNamedDiscourses), {
			path: "/",
		});
	};

	const handleSubmit = (event) => {
		event.preventDefault();
		if (newDiscourse.includes(" ") || discourseExists()) {
			return;
		}

		let nextDiscourseNames = [...discourseNames, nameInput];
		let nextNamedDiscourses = [...namedDiscourses, newDiscourse];

		updateDiscourses({ nextDiscourseNames, nextNamedDiscourses });

		setNameInput("");
		setNewDiscourse([" ", " ", " ", " "]);
	};

	const handleNameInputChange = (event) => {
		setNameInput(event.target.value);
	};

	const removeDiscourse = (discourseName) => {
		let index = discourseNames.indexOf(discourseName);

		let nextDiscourseNames = discourseNames;
		let nextNamedDiscourses = namedDiscourses;

		nextDiscourseNames.splice(index, 1);
		nextNamedDiscourses.splice(index, 1);

		updateDiscourses({ nextDiscourseNames, nextNamedDiscourses });
	};

	const selectAndCopy = (event) => {
		if (!event.target.value) return;
		event.target.select();
		document.execCommand("copy");
		setExportData("");
		setPlaceholder("Copied into clipboard.");
	};

	return (
		<Draggable handle=".name-table-title" bounds=".main-container">
			<div className="name-table">
				<NameTableTitle handleCloseTable={handleCloseTable} />
				<div className="name-table-inner">
					<div className="name-table-container">
						<NameTableHeader />
						{namedDiscourses && discourseNames ? (
							<NameTableBody
								namedDiscourses={namedDiscourses}
								discourseNames={discourseNames}
								removeDiscourse={removeDiscourse}
							/>
						) : null}
						<TableInput
							nameInput={nameInput}
							handleNameInputChange={handleNameInputChange}
							handleSubmit={handleSubmit}
							newDiscourse={newDiscourse}
							setNewDiscourse={setNewDiscourse}
						/>
					</div>
				</div>
				<div className="table-footer">
					<button
						className="generic-button"
						onClick={importDiscourses}
					>
						Import
					</button>
					<button
						className="generic-button"
						onClick={exportDiscourses}
					>
						Export
					</button>
					<button
						className="generic-button"
						onClick={handleTableReset}
					>
						Reset
					</button>
					<textarea
						className="textbox"
						value={exportData}
						onChange={(event) => setExportData(event.target.value)}
						placeholder={placeholder}
						onClick={selectAndCopy}
					/>
				</div>
			</div>
		</Draggable>
	);
};
