import { NavLink } from "react-router-dom";
import "./graphsettings.css";

export const GraphSettings = ({ graphInfo, setGraphInfo }) => {
	const operationNames = [
		"Agent \u2194 Truth",
		"Other \u2194 Agent",
		"Product \u2194 Other",
		"Truth \u2194 Product",
	];
	const operations = ["swAT", "swOA", "swPO", "swTP"];

	const handeOperationSelect = (index) => {
		console.log({ graphInfo });
		switch (index) {
			case "rotation":
				let tempInfo = [...graphInfo];
				let rotationElement = tempInfo.find(
					(element) => element.opname === "rotation"
				);
				let rotationIndex = tempInfo.findIndex(
					(element) => element === rotationElement
				);
				console.log({ rotationIndex }, { rotationElement });
				if (rotationElement) {
					let splicedElement = tempInfo.splice(rotationIndex, 1);
					setGraphInfo(tempInfo);
					console.log({ splicedElement }, { graphInfo });
				} else {
					tempInfo.push({ opname: "rotation", length: 20 });
					setGraphInfo(tempInfo);
					console.log({ graphInfo });
				}
				break;

			default:
				let findElement = graphInfo.find(
					(element) => element.opname === operations[index]
				);
				let elementIndex = graphInfo.findIndex(
					(element) => element === findElement
				);
				if (findElement) {
					let tempInfo = [...graphInfo];
					tempInfo.splice(elementIndex, 1);
					setGraphInfo(tempInfo);
				} else {
					let tempInfo = [...graphInfo];
					tempInfo.push({
						opname: operations[index],
						length: 100,
					});
					setGraphInfo(tempInfo);
				}
		}
	};

	const handleLengthInput = (event, index) => {
		let tempInfo = [...graphInfo];
		switch (index) {
			case "rotation":
				tempInfo.find(
					(element) => element.opname === "rotation"
				).length = event.target.value;
				setGraphInfo(tempInfo);
				console.log({ graphInfo });
				break;
			default:
				let findElement = tempInfo.find(
					(element) => element.opname === operations[index]
				);
				findElement.length = event.target.value;
				setGraphInfo(tempInfo);
		}
	};

	const displayGraphSettings = () => {
		return operations.map((item, index) => {
			let findElement = graphInfo.find(
				(element) => element.opname === item
			);
			let settingClass = findElement
				? "graph-operation-selected"
				: "graph-operation";

			let selectedLength = findElement ? findElement.length : null;
			return (
				<div className={settingClass}>
					<div
						className="operation-name-box"
						onClick={() => handeOperationSelect(index)}
					>
						{findElement ? "☑ " : "☐ "}
						{operationNames[index]}
					</div>
					<input
						className="length-input"
						type="number"
						min="1"
						defaultValue={selectedLength}
						disabled={!findElement}
						onChange={(event) => handleLengthInput(event, index)}
					></input>
				</div>
			);
		});
	};

	let findRotation = graphInfo.find(
		(element) => element.opname === "rotation"
	);

	return (
		<div className="graph-settings-box">
			<h4>3D Graph Settings:</h4>
			<div className="settings-list-header">
			<span>Operations</span><span>Length</span></div>
			<div className="settings-list">
				<div
					className={
						findRotation
							? "graph-operation-selected"
							: "graph-operation"
					}
				>
					<div
						className="operation-name-box"
						onClick={() => handeOperationSelect("rotation")}
					>
						{findRotation ? "☑ " : "☐ "}
						{"Rotation"}
					</div>
					<input
						className="length-input"
						type="number"
						min="1"
						defaultValue={findRotation ? findRotation.length : null}
						disabled={!findRotation}
						onChange={(event) =>
							handleLengthInput(event, "rotation")
						}
					></input>
				</div>
				{displayGraphSettings()}
			</div>
			<div className="graph-button-box">
				<button className="graph-reset-button generic-button"
				onClick={() => setGraphInfo([])}>
					Reset
				</button>
				<NavLink to="/3d-graph">
					<button className="graph-button generic-button">
						View 3D Graph
					</button>
				</NavLink>
			</div>
		</div>
	);
};
