import { nameDisplay } from "../../elements/namedisplay/namedisplay";
import { ringNameDisplay } from "../../elements/ringnamedisplay";

import "./ringdisplay.css";

export const RingDisplay = ({
	displayValues,
	namedDiscourses,
	discourseNames,
	rotateDiscourse,
	setSelectedMathemes,
	ringNames,
	ringSequences,
	reorder,
	handleRenameRing
}) => {
	return (
		<div className="ring-display">
			<div className="ring-title" title="Click to rename ring" onClick={() => handleRenameRing(displayValues)}>
				{ringNameDisplay(ringNames, ringSequences, displayValues, reorder)}
			</div>
			<div
				className="in-ring-name"
				onClick={() => setSelectedMathemes(displayValues)}
			>
				{nameDisplay(
					namedDiscourses,
					discourseNames,
					displayValues,
					" "
				)}
			</div>
			<div
				className="in-ring-name"
				onClick={() =>
					setSelectedMathemes(rotateDiscourse(displayValues, 1))
				}
			>
				{nameDisplay(
					namedDiscourses,
					discourseNames,
					rotateDiscourse(displayValues, 1),
					" "
				)}
			</div>
			<div
				className="in-ring-name"
				onClick={() =>
					setSelectedMathemes(rotateDiscourse(displayValues, 3))
				}
			>
				{nameDisplay(
					namedDiscourses,
					discourseNames,
					rotateDiscourse(displayValues, 3),
					" "
				)}
			</div>
			<div
				className="in-ring-name"
				onClick={() =>
					setSelectedMathemes(rotateDiscourse(displayValues, 2))
				}
			>
				{nameDisplay(
					namedDiscourses,
					discourseNames,
					rotateDiscourse(displayValues, 2),
					" "
				)}
			</div>
		</div>
	);
};
