import "./ringnamedisplay.css";

export const ringNameDisplay = (
	ringNames,
	ringSequences,
	displayValues,
	reorder
) => {
	if (displayValues.includes(" ")) {
		return false;
	}

	let reorderedArray = reorder(displayValues);
	let discourseIndex = ringSequences.indexOf(
		ringSequences.find((item) =>
			item.every((element, index) => element === reorderedArray[index])
		)
	);

	return ringNames[discourseIndex] || "Unknown Ring";
};
