import React from "react";

import "./tablerow.css";

export const TableRow = ({ row, discourseName, removeDiscourse }) => {

  return (
    <React.Fragment>
      <span className="discourse-name">{discourseName}</span>
      {row.map((item, key) => (
        <span className="matheme-cell" key={key}>{item}</span>
      ))}
      <button className="remove-button text-button" onClick={() => removeDiscourse(discourseName)}>[-]</button>
    </React.Fragment>
  );
};
