import { useState, useEffect } from "react";
import Cookies from "universal-cookie";
import { generateGraph } from "../../elements/generategraph";

const useGrid = ({
	tableOpen,
	setTableOpen,
	discourseNames,
	namedDiscourses,
	setNewDiscourse,
	setOverviewOpen,
	ringNames,
	ringSequences,
	selectedOperation,
	setSelectedOperation,
	setGraphData,
	graphInfo
}) => {
	const [selectedMathemes, setSelectedMathemes] = useState([
		" ",
		" ",
		" ",
		" ",
	]);

	const rotateDiscourse = (mathemearray, rotation) => {
		let array = [...mathemearray];
		rotation = (rotation % 4) + 4;
		for (let i = 0; i < rotation; i++) {
			let lastElement = array.pop();
			array.unshift(lastElement);
		}
		return array;
	};

	const handleRotate = (direction) => {
		let rotation;

		switch (direction) {
			case "cw":
				rotation = -1;
				break;
			case "ccw":
				rotation = 1;
				break;

			default:
				console.log("handleRotate Exception FCUK");
				break;
		}

		setSelectedMathemes(
			Object.assign([], rotateDiscourse(selectedMathemes, rotation))
		);
	};

	const customOperation = (discourse, opcode) => {
		let array = [...discourse];
		const swapPlaces = (a, b, arr) => ([arr[a], arr[b]] = [arr[b], arr[a]]);
		switch (opcode) {
			// // switch agent (0) & other (1)
			case 1:
				swapPlaces(0, 1, array);
				break;
			// // switch other & product
			case 2:
				swapPlaces(1, 2, array);
				break;
			// // switch product & truth
			case 3:
				swapPlaces(2, 3, array);
				break;
			// else swapPlaces(3, 0, array);
			case 0:
			default:
				swapPlaces(3, 0, array);
		}

		// // switch other & product
		// // switch agent (0) & other (1)
		// if (opcode === 1) swapPlaces(0, 1, array);
		// // switch other & product
		// else if (opcode === 2) swapPlaces(1, 2, array);
		// // switch product & truth
		// else if (opcode === 3) swapPlaces(2, 3, array);
		// // switch truth & agent (no opcode, default)
		// // e.g. creates capitalist's ring from master's ring
		// else swapPlaces(3, 0, array);
		return array;
	};

	const handleClear = (event) => {
		setSelectedMathemes(Object.assign([], [" ", " ", " ", " "]));
	};

	const quickAdd = (discourse) => {
		if (discourse.includes(" ")) return;
		if (!tableOpen) setTableOpen(true);
		setNewDiscourse(discourse);
	};

	const handleCloseOverview = (event) => {
		setOverviewOpen(false);
	};

	const handleToggle = (setToggleState) => {
		setToggleState((prevState) => !prevState);
	};

	const reorder = (displayValues) => {
		let discourse = [...displayValues];
		let start = discourse.splice(
			discourse.findIndex((i) => i === "S\u2081")
		);
		return start.concat(discourse);
	};

	const handleRenameRing = (displayValues) => {
		const cookies = new Cookies();
		let newRingName = prompt("Enter New Ring Name: ", "");
		if (!newRingName || newRingName === "") return;

		let values = reorder(displayValues);
		let index = ringSequences.indexOf(
			ringSequences.find((sequence) =>
				sequence.every((value, index) => value === values[index])
			)
		);
		let newRingNames = [...ringNames];
		let newRingSequences = [...ringSequences];

		index === -1
			? newRingNames.push(newRingName) && newRingSequences.push(values)
			: newRingNames.splice(index, 1, newRingName);

		cookies.set("ringNames", JSON.stringify(newRingNames), {
			path: "/",
		});
		cookies.set("ringSequences", JSON.stringify(newRingSequences), {
			path: "/",
		});
		handleToggle(setOverviewOpen);
	};

	const handleCreateGraph = () => {
		let graphData = generateGraph({
			selectedOperation,
			namedDiscourses,
			discourseNames,
			rotateDiscourse,
			customOperation,
			ringNames,
			ringSequences,
			reorder,
			graphInfo
		});
		setGraphData(graphData);
	};

	let selectorProps = {
		selectedMathemes,
		setSelectedMathemes,
	};

	let discourseList = {
		namedDiscourses,
		discourseNames,
	};

	useEffect(() => {
		handleCreateGraph()
	}, [graphInfo])

	return {
		selectorProps,
		discourseList,
		quickAdd,
		selectedMathemes,
		setSelectedMathemes,
		rotateDiscourse,
		customOperation,
		handleRotate,
		handleClear,
		handleCloseOverview,
		handleRenameRing,
		reorder,
		handleToggle,
		handleCreateGraph,
	};
};

export default useGrid;
