import React from "react";
import { MathemeSelector } from "../../elements/mathemeselector";

import "./tableinput.css";

export const TableInput = ({
  nameInput,
  handleSubmit,
  handleNameInputChange,
  newDiscourse,
  setNewDiscourse,
}) => {
  const displaySelectRow = () => {
    return newDiscourse.map((_, index) => {
      return (
        <MathemeSelector
          className="new-discourse-selector"
          key={index}
          index={index}
          selectedMathemes={newDiscourse}
          setSelectedMathemes={setNewDiscourse}
          selectedValue={newDiscourse[index]}
        />
      );
    });
  };

  return (
    <form className="input-form" onSubmit={handleSubmit}>
      <input
        className="discourse-input"
        placeholder="New Discourse"
        value={nameInput}
        onChange={handleNameInputChange}
        required
      />
      {displaySelectRow()}
      <button className="add-button text-button">[+]</button>
    </form>
  );
};
