import "./namedisplay.css";

export const nameDisplay = (
  namedDiscourses,
  discourseNames,
  selectedMathemes,
  emptyReturn = ""
) => {
  if (!namedDiscourses || !discourseNames || selectedMathemes.includes(" ")) return emptyReturn;

  let isEqual = namedDiscourses.find((discourse) =>
    discourse.every((value, index) => value === selectedMathemes[index])
  );

  let discourse = discourseNames[namedDiscourses.indexOf(isEqual)];
  return isEqual !== undefined ? discourse : "Unknown Discourse";
};
