import { nameDisplay } from "../namedisplay/namedisplay";
import { ringNameDisplay } from "../ringnamedisplay";

// Default seed.
const seed = ["S\u2082", "a", "$", "S\u2081"];
const defaultColor = "red";
const colors = ["salmon", "plum", "darkblue", "teal", "green"];
const rotationColor = "red";
const operationNames = ["swAT", "swOA", "swPO", "swTP"];
const operationColors = ["magenta", "lime", "turquoise", "yellow"];
const operationLengths = [120, 120, 120, 120];
const greyLength = 40;

// Generates the 3D graph.
export const generateGraph = ({
	selectedOperation,
	namedDiscourses,
	discourseNames,
	rotateDiscourse,
	customOperation,
	ringNames,
	ringSequences,
	reorder,
	graphInfo,
}) => {
	// Creates a ring from the given seed.
	const createRing = (color = defaultColor, ringSeed = seed) =>
		[...Array(4).keys()].map((rotation) => {
			let discourse = rotateDiscourse(ringSeed, rotation);
			let name = nameDisplay(namedDiscourses, discourseNames, discourse);
			let id = JSON.stringify(discourse);

			return {
				discourse,
				name,
				id,
				color,
				ringName: ringNameDisplay(
					ringNames,
					ringSequences,
					ringSeed,
					reorder
				),
			};
		});

	// Creates all 24 nodes.
	const createNodes = () => {
		// Create base ring.
		const baseRing = createRing();
		// All six possible rings.
		const possibleRings =
			// Create four neighboring rings.
			baseRing.map((element, index) => {
				let newSeed = customOperation(element.discourse, 0);
				return createRing(colors[index], newSeed);
			});
		// Create the opposite ring.
		possibleRings.push(
			(() => {
				let newSeed = customOperation(
					customOperation(baseRing[0].discourse),
					2
				);
				return createRing(colors[4], newSeed);
			})()
		);

		// Push the base ring
		possibleRings.push(baseRing);

		return possibleRings.flat().map(({ id, name, color, ringName }) => ({
			id,
			name,
			color,
			ringName,
		}));
	};

	const createLinks = (nodes, operations) => {
		// Rotations within rings.
		const createRotationLinks = () => {
			if (!graphInfo.find((item) => item.opname === "rotation")) return [];
			let rotationLength = graphInfo.find(
				(item) => item.opname === "rotation"
			).length;
			return nodes.map((node) => {
				let target = JSON.stringify(
					rotateDiscourse(JSON.parse(node.id), 1)
				);
				return {
					name: "rotation",
					sourceName: node.name,
					targetName: nodes.find((node) => node.id === target).name,
					color: rotationColor,
					source: node.id,
					target,
					length: rotationLength,
				};
			});
		};

		const createOperationLinks = (opcode) => {
			if (opcode === -1) return;
			return nodes.map((node) => {
				let target = JSON.stringify(
					customOperation(JSON.parse(node.id), opcode)
				);
				let operationLength = graphInfo.find(
					(item) => item.opname === operationNames[opcode]
				).length;
				console.log(operationLength);
				return {
					name: operationNames[opcode],
					sourceName: node.name,
					targetName: nodes.find((node) => node.id === target).name,
					color: operationColors[opcode],
					source: node.id,
					target,
					length: operationLength,
				};
			});
		};

		let links = [
			...createRotationLinks(),
			...[...operations].flatMap(createOperationLinks),
		];

		return links;
	};

	const selectedOperations = graphInfo
		.map((item) => {
			let opcode = operationNames.indexOf(item.opname);
			return opcode;
		})
		.filter((element) => element >= 0);

	console.log({ selectedOperations });
	const nodes = createNodes();
	const links = createLinks(nodes, [...selectedOperations]);
	return {
		nodes,
		links,
	};
};
