import Draggable from "react-draggable";
import React from "react";

import { RingDisplay } from "../ringdisplay/ringdisplay.js";

import "./overview.css";

export const Overview = ({
	discourseList,
	rotateDiscourse,
	setSelectedMathemes,
	handleCloseOverview,
	ringNames,
	ringSequences,
	customOperation,
	reorder,
	handleRenameRing,
}) => {
	const displayOverviewContent = () => {
		const seed = ["S\u2082", "a", "$", "S\u2081"];
		return (
			<React.Fragment>
				{[...Array(6).keys()].map((index) => (
					<RingDisplay
						displayValues={
							index === 0
								? seed
								: index === 5
									? customOperation(customOperation(seed), 2)
									: customOperation(seed, index % 4)
						}
						rotateDiscourse={rotateDiscourse}
						setSelectedMathemes={setSelectedMathemes}
						{...discourseList}
						ringNames={ringNames}
						ringSequences={ringSequences}
						reorder={reorder}
						handleRenameRing={handleRenameRing}
					/>
				))}
			</React.Fragment>
		);
	};

	return (
		<Draggable handle=".overview-title" bounds=".main-container">
			<div className="overview">
				<div className="overview-title">
					Overview (All Rings)
					<button
						className="text-button close-overview-button"
						onClick={handleCloseOverview}
					>
						[<b>&#x2613;</b>]
					</button>
				</div>
				<div className="overview-body">{displayOverviewContent()}</div>
			</div>
		</Draggable>
	);
};
