import React from "react";
import { MathemeSelector } from "../../elements/mathemeselector";
import { nameDisplay } from "../../elements/namedisplay/namedisplay";
import {
	bar,
	impossibility,
	impotence,
	necessity,
	contingency,
} from "../../../assets";

import "./discourseselector.css";

export const DiscourseSelector = ({
	selectedMathemes,
	setSelectedMathemes,
	namedDiscourses,
	discourseNames,
	quickAdd,
}) => {
	const displaySelectors = () => {
		return selectedMathemes.map((_, index) => {
			return (
				<MathemeSelector
					className="matheme-selector"
					key={index}
					index={index}
					selectedMathemes={selectedMathemes}
					setSelectedMathemes={setSelectedMathemes}
					selectedValue={selectedMathemes[index]}
				/>
			);
		});
	};

	return (
		<div className="discourse-container">
			<div className="discourse-header">
				<div className="name-display">
					{nameDisplay(
						namedDiscourses,
						discourseNames,
						selectedMathemes,
						"Input Discourse: "
					)}{" "}
				</div>
				<button
					className="text-button quick-add-button"
					onClick={() => quickAdd(selectedMathemes)}
          title="Quick add this discourse"
				>
					[+]
				</button>
			</div>
			<form className="discourse-selector">
				{displaySelectors()}
				<div className="image-container bar-image-container">
					<img
						className="bar"
						alt="bar"
						src={bar}
						draggable="false"
					/>
				</div>
				<div className="image-container bar-image-container">
					<img
						className="bar"
						alt="bar"
						src={bar}
						draggable="false"
					/>
				</div>
				<div className="image-container impossibility-image-container">
					<img
						className="impossibility"
						alt="impossibility"
						title="impossibility"
						src={impossibility}
						draggable="false"
					/>
				</div>
				<div className="image-container impotence-image-container">
					<img
						className="impotence"
						alt="impotence"
						title="impotence"
						src={impotence}
						draggable="false"
					/>
				</div>
				<div className="image-container necessity-image-container">
					<img
						className="necessity"
						alt="necessity"
						title="necessity"
						src={necessity}
						draggable="false"
					/>
				</div>
				<div className="image-container contingency-image-container">
					<img
						className="contingency"
						alt="contingency"
						title="contingency"
						src={contingency}
						draggable="false"
					/>
				</div>
			</form>
		</div>
	);
};
