import "./discoursedisplay.css";
import { MathemeDisplay } from "../../elements/mathemedisplay";
import { nameDisplay } from "../../elements/namedisplay/namedisplay";

import {
	bar,
	impossibility,
	impotence,
	necessity,
	contingency,
} from "../../../assets";

export const DiscourseDisplay = ({
	displayValues,
	namedDiscourses,
	discourseNames,
	quickAdd,
}) => {
	return (
		<div className="discourse-container">
			<div className="discourse-header">
				<div>
					{nameDisplay(
						namedDiscourses,
						discourseNames,
						displayValues,
						" "
					)}
				</div>
				<button
					className="text-button quick-add-button"
					onClick={() => quickAdd(displayValues)}
					title="Quick add this discourse"
				>
					[+]
				</button>
			</div>
			<form className="discourse-display">
				<MathemeDisplay displayValue={displayValues[0]} />
				<MathemeDisplay displayValue={displayValues[1]} />
				<MathemeDisplay displayValue={displayValues[2]} />
				<MathemeDisplay displayValue={displayValues[3]} />
				<div className="image-container bar-image-container">
					<img
						className="bar"
						alt="bar"
						src={bar}
						draggable="false"
					/>
				</div>
				<div className="image-container bar-image-container">
					<img
						className="bar"
						alt="bar"
						src={bar}
						draggable="false"
					/>
				</div>
				<div className="image-container impossibility-image-container">
					<img
						className="impossibility"
						alt="impossibility"
						title="impossibility"
						src={impossibility}
						draggable="false"
					/>
				</div>
				<div className="image-container impotence-image-container">
					<img
						className="impotence"
						alt="impotence"
						title="impotence"
						src={impotence}
						draggable="false"
					/>
				</div>
				<div className="image-container necessity-image-container">
					<img
						className="necessity"
						alt="necessity"
						title="necessity"
						src={necessity}
						draggable="false"
					/>
				</div>
				<div className="image-container contingency-image-container">
					<img
						className="contingency"
						alt="contingency"
						title="contingency"
						src={contingency}
						draggable="false"
					/>
				</div>
			</form>
		</div>
	);
};
