import React, { useState } from "react";
import {
	BrowserRouter as Router,
	Switch,
	Route,
	HashRouter,
} from "react-router-dom";

import { Home, GraphView } from "./pages";

import "./App.css";

const App = () => {
	const [graphData, setGraphData] = useState();
	const [graphInfo, setGraphInfo] = useState([
		// { opname: "rotation", length: 10 },
		// { opname: "swAT", length: 250 },
		// { opname: "swOA", length: 400 },
		// { opname: "swPO", length: 200 },
		// { opname: "swTP", length: 200 },
	]);

	return (
		<HashRouter basename="/">
			<Switch>
				<Route path="/420">
					<h1>Blaze It Faggot</h1>
				</Route>
				<Route path="/3d-graph">
					<GraphView graphData={graphData} />
				</Route>
				<Route path="/">
					<Home
						graphData={graphData}
						setGraphData={setGraphData}
						graphInfo={graphInfo}
						setGraphInfo={setGraphInfo}
					/>
				</Route>
			</Switch>
		</HashRouter>
	);
};

export default App;
