import React from "react";
import "./mathemeselector.css";

export const MathemeSelector = ({
  index,
  selectedMathemes,
  setSelectedMathemes,
  selectedValue,
  className
}) => {

  const handleSelect = (event) => {
    let { value } = event.target;
    let selectedValues = selectedMathemes;

    value === " "
      ? (selectedValues[index] = " ")
      : (selectedValues[index] = event.target.value);
    setSelectedMathemes(Object.assign([], [...selectedValues]));
  };

  const displayOptions = () => {
    return [
      { value: " ", displayValue: " " },
      { value: "S\u2081", displayValue: "S\u2081" },
      { value: "S\u2082", displayValue: "S\u2082" },
      { value: "$", displayValue: "$" },
      { value: "a", displayValue: "a" },
    ].map((matheme) => {
      return (
        <option
          key={matheme.value}
          value={matheme.value}
          selected={matheme.value === selectedValue}
          disabled={
            matheme.value !== " " && selectedMathemes.includes(matheme.value)
          }
        >
          {matheme.value}
        </option>
      );
    });
  };

  return (
    <select
      className={className}
      name="mathemeInput"
      onChange={handleSelect}
    >
      {displayOptions()}
    </select>
  );
};
